import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import "./header.css";
import "./Nav.css";

class Nav extends Component {
  state = {};

  render() {
    function Home() {
      return "tyest";
    }
    return (
      <div>
        <div className="container-fluid">
          <div className="row justify-content-between">
            <div className="col-md-2">
              <h1 className="my-logo">JR</h1>
            </div>
            <div className="col-md-4 my-col-nav">
              <ul className="nav navbar-nav d-none d-md-block">
                <b>
                  <Router>
                    <a className="text-dark text-decoration-none" href="/">
                      <span className="" /> Home
                    </a>
                    <a className="text-dark text-decoration-none" href="/about">
                      <span className="glyphicon glyphicon-user" /> About
                    </a>
                    <a className="text-dark text-decoration-none" href="#">
                      <span className="glyphicon glyphicon-user" /> Portfolio
                    </a>
                    <a className="text-dark text-decoration-none" href="#">
                      <span className="glyphicon glyphicon-user" /> Contact
                    </a>
                    <Route path="/about" exact Component={Home} />
                  </Router>
                </b>
              </ul>
              <div className="dropdown d-md-none my-col-nav-collapsed">
                <button className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                  Navigation
                </button>
                <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                  <a className="dropdown-item" href="#">
                    Home
                  </a>
                  <a className="dropdown-item" href="#">
                    About
                  </a>
                  <a className="dropdown-item" href="#">
                    Portfolio
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Nav;
